// FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//BrandonGrotesque
//bold
@font-face {
	@include fontdefault(normal, 700, 'BrandonGrotesque');
	@include fontpath('BrandonGrotesque-Bold', 'BrandonGrotesque-Bold', 'BrandonGrotesque-Bold'); }

//medium
@font-face {
	@include fontdefault(normal, 700, 'BrandonGrotesque');
	@include fontpath('BrandonGrotesque-Medium', 'BrandonGrotesque-Medium', 'BrandonGrotesque-Medium'); }

//regular
@font-face {
	@include fontdefault(normal, 400, 'BrandonGrotesque');
	@include fontpath('BrandonGrotesque-Regular', 'BrandonGrotesque-Regular', 'BrandonGrotesque-Regular'); }

//regular
@font-face {
	@include fontdefault(normal, 300, 'BrandonGrotesque');
	@include fontpath('BrandonGrotesque-Light', 'BrandonGrotesque-Light', 'BrandonGrotesque-Light'); }
