.btn {
  background-color: transparent;
  border: 2px solid white;
  border-radius: 2px;
  padding: 11px 65px 15px;
  font-size: 23px;
  font-family: BrandonGrotesque, Open Sans, sans-serif;
  color: white;
  font-weight: 700;
  transition: color 2s background-color 2s;
  position: relative;
  overflow: hidden;
  border-radius: 2px;
  z-index: 1;
}

.btn:before {
  content: '';
  position: absolute;
  left: -20px;
  width: 10px;
  height: 10px;
  background-color: #7690c5;
  border-radius: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.btn:after {
  content: '';
  position: absolute;
  right: -20px;
  width: 10px;
  height: 10px;
  background-color: #7690c5;
  border-radius: 50%;
  top: 50%;
  transform: translate(50%, -50%);
  z-index: -1;
}

.btn:hover:after {
  animation: criss-cross-right 1s both;
  animation-direction: alternate;
}

.btn:hover:before {
  animation: criss-cross-left 1s both;
  animation-direction: alternate;
}

@keyframes criss-cross-left {
  0% {
    left: -20px;
  }
  70% {
    left: 50%;
    width: 20px;
    height: 20px;
  }
  100% {
    left: 50%;
    width: 375px;
    height: 375px;
  }
}

@keyframes criss-cross-right {
  0% {
    right: -20px;
  }
  70% {
    right: 50%;
    width: 20px;
    height: 20px;
  }
  100% {
    right: 50%;
    width: 375px;
    height: 375px;
  }
}

