.modal {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  left: 0;
  top: 0;
  z-index: 10;
  transform: translateY(-100%);
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: transform 0.5s;
}

.modal.open {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.modal__icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
}

.modal__icon:before {
  content: '';
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.modal__icon:after {
  content: '';
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}

